import React, { useEffect, useRef } from 'react';
import { motion } from 'framer-motion';
import { Sparkles, Monitor, TrendingUp, Smartphone, DollarSign, Globe, Cpu, Bot } from 'lucide-react';
import './HomePage.css';

const HomePage = ({ setCurrentSection }) => {
  const homeRef = useRef(null);
  const cardRefs = useRef([]);

  useEffect(() => {
    const optimizeScroll = () => {
      document.body.style.overflowX = 'hidden';
      document.documentElement.style.overflowX = 'hidden';
    };
    optimizeScroll();

    const angle = 20;
    const lerp = (start, end, amount) => (1 - amount) * start + amount * end;
    const remap = (value, oldMax, newMax) => {
      const newValue = ((value + oldMax) * (newMax * 2)) / (oldMax * 2) - newMax;
      return Math.min(Math.max(newValue, -newMax), newMax);
    };

    const cards = cardRefs.current;
    cards.forEach((e) => {
      if (!e) return;
      const handleMouseMove = (event) => {
        const rect = e.getBoundingClientRect();
        const centerX = (rect.left + rect.right) / 2;
        const centerY = (rect.top + rect.bottom) / 2;
        const posX = event.pageX - centerX;
        const posY = event.pageY - centerY;
        e.dataset.rotateX = remap(posX, rect.width / 2, angle);
        e.dataset.rotateY = -remap(posY, rect.height / 2, angle);
      };

      const handleMouseOut = () => {
        e.dataset.rotateX = 0;
        e.dataset.rotateY = 0;
      };

      e.addEventListener('mousemove', handleMouseMove);
      e.addEventListener('mouseout', handleMouseOut);

      return () => {
        e.removeEventListener('mousemove', handleMouseMove);
        e.removeEventListener('mouseout', handleMouseOut);
      };
    });

    const update = () => {
      cards.forEach((e) => {
        if (!e) return;
        let currentX = parseFloat(e.style.getPropertyValue('--rotateY')?.slice(0, -3) || 0);
        let currentY = parseFloat(e.style.getPropertyValue('--rotateX')?.slice(0, -3) || 0);
        const x = lerp(currentX, e.dataset.rotateX || 0, 0.05);
        const y = lerp(currentY, e.dataset.rotateY || 0, 0.05);
        e.style.setProperty('--rotateY', `${x}deg`);
        e.style.setProperty('--rotateX', `${y}deg`);
      });
    };

    const interval = setInterval(update, 1000 / 60);
    return () => clearInterval(interval);
  }, []);

  const cardVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: (i) => ({
      opacity: 1,
      y: 0,
      transition: { duration: 0.6, ease: 'easeOut', delay: i * 0.1 },
    }),
  };

  const services = [
    {
      title: '3D Anamorphic Illusions',
      description: 'Create mesmerizing visual experiences with cutting-edge anamorphic illusions that captivate audiences and elevate brand presence.',
      icon: Sparkles,
      color: '#00FFFF',
      shadow: 'https://images.pexels.com/photos/7135037/pexels-photo-7135037.jpeg',
      background: 'https://images.pexels.com/photos/7135037/pexels-photo-7135037.jpeg',
      cutout: 'https://images.pexels.com/photos/7135037/pexels-photo-7135037.jpeg',
      border: 'border-left-behind',
    },
    {
      title: '3D Billboard Advertising',
      description: 'Transform urban landscapes with immersive 3D billboard campaigns that leverage anamorphic technology for maximum impact.',
      icon: Monitor,
      color: '#FF6600',
      shadow: 'https://images.pexels.com/photos/546819/pexels-photo-546819.jpeg',
      background: 'https://images.pexels.com/photos/546819/pexels-photo-546819.jpeg',
      cutout: 'https://images.pexels.com/photos/546819/pexels-photo-546819.jpeg',
      border: 'border-right-behind border-bottom-behind',
    },
    {
      title: 'Digital Business & IT Solutions',
      description: 'Streamline operations with enterprise-grade ERP, CRM, and cybersecurity solutions tailored to your business needs.',
      icon: Cpu,
      color: '#4169E1',
      shadow: 'https://images.pexels.com/photos/3183150/pexels-photo-3183150.jpeg',
      background: 'https://images.pexels.com/photos/3183150/pexels-photo-3183150.jpeg',
      cutout: 'https://images.pexels.com/photos/3183150/pexels-photo-3183150.jpeg',
      border: 'border-left-behind',
    },
    {
      title: 'Digital Marketing',
      description: 'Drive growth with data-driven digital marketing strategies that maximize ROI and enhance brand visibility.',
      icon: TrendingUp,
      color: '#FF00FF',
      shadow: 'https://images.pexels.com/photos/265087/pexels-photo-265087.jpeg',
      background: 'https://images.pexels.com/photos/265087/pexels-photo-265087.jpeg',
      cutout: 'https://images.pexels.com/photos/265087/pexels-photo-265087.jpeg',
      border: 'border-right-behind',
    },
  ];

  const products = [
    {
      title: 'Pixel 8',
      description: 'Experience the future with Pixel 8, a cutting-edge device powered by advanced AI and seamless connectivity.',
      icon: Smartphone,
      color: '#008080',
      shadow: 'https://images.pexels.com/photos/607812/pexels-photo-607812.jpeg',
      background: 'https://images.pexels.com/photos/607812/pexels-photo-607812.jpeg',
      cutout: 'https://images.pexels.com/photos/607812/pexels-photo-607812.jpeg',
      border: 'border-left-behind border-bottom-behind',
    },
    {
      title: 'Pixel World',
      description: 'Immerse yourself in Pixel World, a virtual ecosystem blending AR/VR for unparalleled digital experiences.',
      icon: Globe,
      color: '#FF1493',
      shadow: 'https://images.pexels.com/photos/8386440/pexels-photo-8386440.jpeg',
      background: 'https://images.pexels.com/photos/8386440/pexels-photo-8386440.jpeg',
      cutout: 'https://images.pexels.com/photos/8386440/pexels-photo-8386440.jpeg',
      border: 'border-right-behind',
    },
    {
      title: 'Pixel Coin',
      description: 'Secure and transparent transactions with Pixel Coin, our blockchain-based digital currency.',
      icon: DollarSign,
      color: '#FFD700',
      shadow: 'https://images.pexels.com/photos/844124/pexels-photo-844124.jpeg',
      background: 'https://images.pexels.com/photos/844124/pexels-photo-844124.jpeg',
      cutout: 'https://images.pexels.com/photos/844124/pexels-photo-844124.jpeg',
      border: 'border-left-behind',
    },
    {
      title: 'Pixel Robotics',
      description: 'Revolutionize automation with Pixel Robotics, integrating AI and IoT for intelligent robotic solutions.',
      icon: Bot,
      color: '#8A2BE2',
      shadow: 'https://images.pexels.com/photos/1632790/pexels-photo-1632790.jpeg',
      background: 'https://images.pexels.com/photos/1632790/pexels-photo-1632790.jpeg',
      cutout: 'https://images.pexels.com/photos/1632790/pexels-photo-1632790.jpeg',
      border: 'border-right-behind border-bottom-behind',
    },
  ];

  return (
    <div className="home-page" ref={homeRef}>
      <section className="hero-section">
        <motion.h1
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
        >
          Innovate. Transform. Succeed.
        </motion.h1>
        <motion.p
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.2 }}
        >
          Discover our cutting-edge services and products designed to redefine your digital future.
        </motion.p>
      </section>

      <section className="services-section">
        <motion.h2
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6 }}
        >
          Our Services
        </motion.h2>
        <div className="card-container">
          {services.map((service, index) => (
            <motion.div
              key={index}
              className={`card ${service.border}`}
              variants={cardVariants}
              custom={index}
              initial="hidden"
              animate="visible"
              ref={(el) => (cardRefs.current[index] = el)}
              style={{ '--rotateX': '0deg', '--rotateY': '0deg' }}
              role="button"
              tabIndex={0}
            >
              <div className="shadow" style={{ '--url': `url(${service.shadow})` }} />
              <div className="image background" style={{ '--url': `url(${service.background})` }} />
              <div className="image cutout" style={{ '--url': `url(${service.cutout})` }} />
              <div className="content">
                <service.icon size={48} color={service.color} style={{ marginBottom: '1rem' }} />
                <h2 style={{ color: service.color }}>{service.title}</h2>
                <p>{service.description}</p>
              </div>
            </motion.div>
          ))}
        </div>
      </section>

      <section className="products-section">
        <motion.h2
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6 }}
        >
          Our Products
        </motion.h2>
        <div className="card-container">
          {products.map((product, index) => (
            <motion.div
              key={index}
              className={`card ${product.border}`}
              variants={cardVariants}
              custom={index + services.length}
              initial="hidden"
              animate="visible"
              ref={(el) => (cardRefs.current[index + services.length] = el)}
              style={{ '--rotateX': '0deg', '--rotateY': '0deg' }}
              role="button"
              tabIndex={0}
            >
              <div className="shadow" style={{ '--url': `url(${product.shadow})` }} />
              <div className="image background" style={{ '--url': `url(${product.background})` }} />
              <div className="image cutout" style={{ '--url': `url(${product.cutout})` }} />
              <div className="content">
                <product.icon size={48} color={product.color} style={{ marginBottom: '1rem' }} />
                <h2 style={{ color: product.color }}>{product.title}</h2>
                <p>{product.description}</p>
              </div>
            </motion.div>
          ))}
        </div>
      </section>
    </div>
  );
};

export default HomePage;