import LoadingScreen from './components/LoadingScreen/LoadingScreen';
import ParticleSystem from './components/Effects/ParticleSystem';
import GoogleAnalytics from './components/GoogleAnalytics/GoogleAnalytics';

// Service Detail Components
import AdvertisingDetail from './components/Services/3D-Advertising/AdvertisingDetail';
import DigitalMarketingDetail from './components/Services/DigitalMarketing/DigitalMarketingDetail';
import ITSolutionsDetail from './components/Services/ITSolutions/ITSolutionsDetail';
import IoTSolutionsDetail from './components/Services/IoTSolutions/IoTSolutionsDetail';
import DigitalExperienceDetail from './components/Services/DigitalExperience/DigitalExperienceDetail';
import UIUXDesignDetail from './components/Services/UIUXDesign/UIUXDesignDetail';
import BlockchainDetail from './components/Services/Blockchain/BlockchainDetail';
import AnimationCreation from './components/Services/AnimationCreation/AnimationCreation';

// 3D Advertising Sub-services
import AnamorphicVideo from './components/Services/3D-Advertising/AnamorphicVideo/AnamorphicVideo';
import DigitalStoreBranding from './components/Services/3D-Advertising/DigitalStoreBranding/DigitalStoreBranding';
import OutdoorBranding from './components/Services/3D-Advertising/OutdoorBranding/OutdoorBranding';

// Digital Marketing Sub-services
import SEOServices from './components/Services/DigitalMarketing/SEO/SEOServices';
import SocialMediaMarketing from './components/Services/DigitalMarketing/SocialMedia/SocialMediaMarketing';
import WebsiteManagement from './components/Services/DigitalMarketing/WebsiteManagement/WebsiteManagement';
import GoogleAdWords from './components/Services/DigitalMarketing/GoogleAdWords/GoogleAdWords';
import GoogleAnalyticsService from './components/Services/DigitalMarketing/GoogleAnalytics/GoogleAnalytics';
import ContentCreation from './components/Services/DigitalMarketing/ContentCreation/ContentCreation';
import PPCAdvertising from './components/Services/DigitalMarketing/PPC/PPCAdvertising';
import CelebrityAdvertising from './components/Services/DigitalMarketing/Celebrity/CelebrityAdvertising';

// IT Solutions Sub-services
import EnterpriseSoftware from './components/Services/ITSolutions/EnterpriseSoftware/EnterpriseSoftware';
import AISystems from './components/Services/ITSolutions/AISystems/AISystems';
import CyberSecurity from './components/Services/ITSolutions/CyberSecurity/CyberSecurity';
import ClientSupport from './components/Services/ITSolutions/ClientSupport/ClientSupport';

// IoT Solutions Sub-services
import ARVRDevelopment from './components/Services/IoTSolutions/ARVR/ARVRDevelopment';
import IoTIntegration from './components/Services/IoTSolutions/IoTIntegration/IoTIntegration';
import Robotics from './components/Services/IoTSolutions/Robotics/Robotics';
import WirelessCommunication from './components/Services/IoTSolutions/Wireless/WirelessCommunication';

// Company Pages
import AboutUs from './components/Company/AboutUs/AboutUs';
import OurTeam from './components/Company/OurTeam/OurTeam';
import Careers from './components/Company/Careers/Careers';
import PressMedia from './components/Company/PressMedia/PressMedia';
import Awards from './components/Company/Awards/Awards';

// Support Pages
import HelpCenter from './components/Support/HelpCenter/HelpCenter';
import ContactSupport from './components/Support/ContactSupport/ContactSupport';
import SystemStatus from './components/Support/SystemStatus/SystemStatus';
import PrivacyPolicy from './components/Support/PrivacyPolicy/PrivacyPolicy';
import TermsOfService from './components/Support/TermsOfService/TermsOfService';

function AppContent() {
  const [loading, setLoading] = useState(true);
  const [currentSection, setCurrentSection] = useState('home');
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(false);
    }, 3500);
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    document.documentElement.style.scrollBehavior = 'smooth';

    // Remove perspective to avoid interference with fixed positioning
    document.body.style.removeProperty('perspective');
    document.body.style.removeProperty('perspective-origin');

    return () => {
      document.documentElement.style.scrollBehavior = 'auto';
    };
  }, []);

  useEffect(() => {
    const path = location.pathname.slice(1) || 'home';
    setCurrentSection(path);

    // Scroll to top on route change with smooth effect
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }, [location]);

  const handleNavClick = (section) => {
    navigate(`/${section}`);
    setCurrentSection(section);
  };

  if (loading) {
    return <LoadingScreen />;
  }

  return (
    <div className="app">
      <GoogleAnalytics /> {/* Google Analytics Component */}
      <ParticleSystem />
      <Header currentSection={currentSection} />
      <main className="main-content" style={{ paddingTop: '4rem' }}>
        <Routes>
          <Route path="/" element={<Navigate to="/home" replace />} />
          <Route path="/home" element={
            <>
              <Hero />
              <HomePage setCurrentSection={setCurrentSection} />
              <About />
              <Contact />
            </>
          } />

          {/* Main Service Categories */}
          <Route path="/services" element={<Services setCurrentSection={handleNavClick} />} />
          <Route path="/3d-advertising" element={<AdvertisingDetail setCurrentSection={handleNavClick} />} />
          <Route path="/digital-marketing" element={<DigitalMarketingDetail setCurrentSection={handleNavClick} />} />
          <Route path="/it-solutions" element={<ITSolutionsDetail setCurrentSection={handleNavClick} />} />
          <Route path="/iot-solutions" element={<IoTSolutionsDetail setCurrentSection={handleNavClick} />} />
          <Route path="/digital-experience" element={<DigitalExperienceDetail setCurrentSection={handleNavClick} />} />
          <Route path="/uiux-design" element={<UIUXDesignDetail setCurrentSection={handleNavClick} />} />
          <Route path="/blockchain" element={<BlockchainDetail setCurrentSection={handleNavClick} />} />
          <Route path="/animation-creation" element={<AnimationCreation setCurrentSection={handleNavClick} />} />

          {/* 3D Advertising Sub-services */}
          <Route path="/anamorphic-video" element={<AnamorphicVideo setCurrentSection={handleNavClick} />} />
          <Route path="/digital-store-branding" element={<DigitalStoreBranding setCurrentSection={handleNavClick} />} />
          <Route path="/outdoor-branding" element={<OutdoorBranding setCurrentSection={handleNavClick} />} />

          {/* Digital Marketing Sub-services */}
          <Route path="/seo-services" element={<SEOServices setCurrentSection={handleNavClick} />} />
          <Route path="/social-media-marketing" element={<SocialMediaMarketing setCurrentSection={handleNavClick} />} />
          <Route path="/website-management" element={<WebsiteManagement setCurrentSection={handleNavClick} />} />
          <Route path="/google-adwords" element={<GoogleAdWords setCurrentSection={handleNavClick} />} />
          <Route path="/google-analytics" element={<GoogleAnalyticsService setCurrentSection={handleNavClick} />} />
          <Route path="/content-creation" element={<ContentCreation setCurrentSection={handleNavClick} />} />
          <Route path="/ppc-advertising" element={<PPCAdvertising setCurrentSection={handleNavClick} />} />
          <Route path="/celebrity-advertising" element={<CelebrityAdvertising setCurrentSection={handleNavClick} />} />

          {/* IT Solutions Sub-services */}
          <Route path="/enterprise-software" element={<EnterpriseSoftware setCurrentSection={handleNavClick} />} />
          <Route path="/ai-systems" element={<AISystems setCurrentSection={handleNavClick} />} />
          <Route path="/cyber-security" element={<CyberSecurity setCurrentSection={handleNavClick} />} />
          <Route path="/client-support" element={<ClientSupport setCurrentSection={handleNavClick} />} />

          {/* IoT Solutions Sub-services */}
          <Route path="/arvr-development" element={<ARVRDevelopment setCurrentSection={handleNavClick} />} />
          <Route path="/iot-integration" element={<IoTIntegration setCurrentSection={handleNavClick} />} />
          <Route path="/robotics" element={<Robotics setCurrentSection={handleNavClick} />} />
          <Route path="/wireless-communication" element={<WirelessCommunication setCurrentSection={handleNavClick} />} />

          {/* Company Pages */}
          <Route path="/about-us" element={<AboutUs setCurrentSection={handleNavClick} />} />
          <Route path="/our-team" element={<OurTeam setCurrentSection={handleNavClick} />} />
          <Route path="/careers" element={<Careers setCurrentSection={handleNavClick} />} />
          <Route path="/press-media" element={<PressMedia setCurrentSection={handleNavClick} />} />
          <Route path="/awards" element={<Awards setCurrentSection={handleNavClick} />} />

          {/* Support Pages */}
          <Route path="/help-center" element={<HelpCenter setCurrentSection={handleNavClick} />} />
          <Route path="/contact-support" element={<ContactSupport setCurrentSection={handleNavClick} />} />
          <Route path="/system-status" element={<SystemStatus setCurrentSection={handleNavClick} />} />
          <Route path="/privacy-policy" element={<PrivacyPolicy setCurrentSection={handleNavClick} />} />
          <Route path="/terms-of-service" element={<TermsOfService setCurrentSection={handleNavClick} />} />
        </Routes>
      </main>
      <Footer setCurrentSection={handleNavClick} />
      {/* Added image at the bottom of the page */}
      <div className="bottom-image-container">
        <img
          src="/public/images/ascii-art-text.png"
          alt="Bottom Page Image"
          style={{
            width: '100%',
            height: 'auto',
            display: 'block',
            objectFit: 'cover',
          }}
        />
      </div>
    </div>
  );
}

function App() {
  return <AppContent />;
}

export default App;